/*
    MODULO LOGIN
*/

.login {
    width: 50%;
    margin: auto;
    padding: 5px;
    background-color: #0f021c;
    color: white;
    border-radius: 10px;
    border-color: white;
}
.contenedor {
    color: white;
    background-color: #041E42;
    padding: 20px;
    height: 100vh;
    display: flex;
    justify-content: space-around;
    text-align: center;
    align-items: center;
    flex-wrap: wrap;
    border-left: 1px white;
    border-left-style: solid;
}

@media (max-width: 1024px) {
    .contenedor {
        height: auto;
    }
}
/*
    MODULO HOME
*/
.box {
    background-color: #557996;
    font-size: 30px;
    width: 250px;
    height: 150px;
    text-align: center;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
}
.box:hover {
    background-color: #94b8d7;
}
/*
    MODULO USUARIOS
*/
.contenedor-usuarios {
    color: blue;
    border-radius: 10px;
    margin: 10px;
    padding: 20px;
    min-height: 500px;
    margin-top: 25px;
}
.contenedor-usuarios > hr {
    color: #f50092;
}
/*
    MÃ“DULO PRODUCTOS
*/
.contenedor-productos {
    color: blue;
    border-radius: 10px;
    margin: 10px;
    padding: 20px;
    min-height: 500px;
}

.contenedor-controlProductos {
    border-radius: 10px;
    min-height: 500px;
    
}

@media (max-width: 550px) {
    .contenedor-controlProductos {
        margin-top: 0;
    }
}

.contenedor-productos > hr {
    color: #f50092;
}

.categoria {
    background-color: #212529;
    margin: 20px;
    border-radius: 10px;
    padding: 5px;

}

.productosCategoria {
    display: flex;
    flex-wrap: wrap;
    color: black;
}

.productCard {
    background-color: #7498b6;
    border-radius: 8px;
    margin-left: 10px;
    margin-top: 10px;
    padding: 5px;
    text-align: center;
    min-width: 120px;
}

.contenedor-ventas {
    color: blue;
    border-radius: 10px;
    margin: 10px;
    padding: 20px;
    min-height: 500px;
}
.contenedor-ventas > hr {
    color: #f50092;
}

@media (max-width: 330px) {
    .contenedor-ventasGeneral {
        margin-top: 30px;
    }
}

.contenedorVentas {
    color: white;
    padding: 10px;
    margin: 0;
    height: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.contenedorProductosVentas {
    width: 70%;
    background-color: #021d34;
    border-radius: 20px;
    padding: 10px;
    overflow-y: scroll;
    height: 600px;
}

.contenedorFormVenta {
    width: 28%;
    background-color: #021d34;
    border-radius: 20px;
    padding: 10px;
    overflow-y: scroll;
    height: 600px;
}

@media(max-width: 700px){
    .contenedorProductosVentas {
        width: 100%;
        height: 100%;
    }
    .contenedorFormVenta {
        width: 100%;
        height: 100%;
    }
}

.CardItem {
    background-color: #557996;
    color: black;
    border-radius: 20px;
    padding: 5px;
    text-align: center;
    margin-top: 5px;
}

.CantidadItem {
    display: flex;
    justify-content: space-around;
}

.btn-c {
    width: 30px;
    height: 30px;
    border-radius: 10px;
}
.divCaja {
    text-align: center;
    margin-top: 100px;
    color:black;
}
.controlCaja {
    padding: 20px;
    border-radius: 20px;
    text-align: center;
}
div.contenedor1{
    width: 250px;
    height: 200px;
    float:left;
    border-radius: 50px;
    -webkit-transition: height 0.4s;
}

@media screen {
    div.contenedor1 {
        width: 150px;
        font-size: 15px;
    }
}



div.contenedor0{
    width: 20px;
    height: 220px;
    float:left;
    -webkit-transition: height 0.4s;
}
div#cero{
    background-color: rgb(255, 255, 255,0);
}
div#uno{
    background-color: rgb(208, 101, 3);
}
div#dos{
    background-color: rgb(233, 147, 26);
}
div#tres{
    background-color: rgb(22, 145, 26);
}
div#cuatro{
    background-color: rgb(22, 107, 162);
}
div#cinco{
    background-color: rgb(27, 54, 71);
}
div#seis{
    background-color: rgb(22, 63, 92);
}
img.icon{
    display: block;
    margin:50px auto;
    background-color: rgba(255, 255,255,0.15);
    width:60px;
    padding: 1px;   
    -webkit-border-radius:42%;
    -webkit-box-shadow: 0px 0px 0px 60px rgba(255,255, 255,0);
    -webkit-transition:box-shadow 0.9;
}
p.texto{
    font-size: 1.2em;
    color: white;
    text-align: center;
    padding-top: 5px;
    opacity: 0.6; 
    -webkit-transition: padding-top 0.4s;
}
div.contenedor1:hover{
    height: 260px;
}
div.contenedor1:hover p.texto{
    padding-top: 50px;
    opacity: 1;
}
div.contenedor1:hover img.icon{
    -webkit-box-shadow:0px 0px 0px 10px rgba(255, 255, 255, 0.5);
}


.newMenuContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
};

.newMenuContainerTitle {
    background-color: #041E42;
    color: white;
}




/* PRINCIPAL NAVIGATION VIEW */
.principalNav {
    background-color: blue;
    height: 100vh;
}

/* HOME VIEW */
.homeView {
    background-color: #041E42;
    height: 99vh;
    display: flex;
}

.homeViewTitle {
    font-size: 60px;
}

.homeViewImage {
    width: 200px;
}

@media (max-width: 500px) {
    .homeView {
        display: flex;
        flex-direction: column;
        width: 100%;
        justify-content: space-around;
        position: absolute;
        padding-left: 100px;

    }
    .homeViewImage {
        width: 250px;
    }
    .homeViewTitle {
        font-size: 15px;
    }
}

.cellBaruch {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    width: 50%;
    border-right: 1px dotted white;
}
.cellBaruch:hover {
    background-color: #072d62;
}

.detailBaruch {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    width: 50%;
}
.detailBaruch:hover {
    background-color: #072d62;
}


/* NAVBAR */
.navbar-media {
    color: white;
    font-size: 25px;
}

@media (max-width: 425px){
    .navbar-media {
        font-size: 14px;
    }
}


/* TITLE MENU */
.titleMenuText {
    position: absolute;
    top: 10px;
    font-size: 50px;
    cursor: pointer;
}

@media (max-width: 425px){
    .titleMenuText {
        top: 0px;
        right: 0px;
        font-size: 20px;
        cursor: pointer;
    }
}

.adminHomeView {
    background-color: #072d62;
    height: 100vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 50px;
}

@media (max-width: 425px){
    .adminHomeView {
        height: 100%;
    }
}


.adminHomeViewBox {
    color: white;
    border: 1px solid gray;
    border-radius: 25px;
    margin: 10px;
    padding: 20px;
}

.adminHomeViewBox:hover {
    opacity: 0.5;
    cursor: pointer;
}